import React, { useState, useCallback, useEffect } from 'react'
import styled from 'styled-components'
import { Card, CardBody, Heading, Text, Button } from '@pancakeswap-libs/uikit'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import BigNumber from 'bignumber.js'
import { useERC20, usePresaleContract, useReferralContract } from 'hooks/useContract'
import Countdown from 'react-countdown'
import BalanceInput from 'components/Input/BalanceInput'
import { getWeb3 } from 'utils/web3'
import { useIfoAllowance } from 'hooks/useAllowance'
import { useIfoApprove } from 'hooks/useApprove'
import contracts from 'config/constants/contracts'

import useI18n from 'hooks/useI18n'
import { useAllHarvest } from 'hooks/useHarvest'
import useFarmsWithBalance from 'hooks/useFarmsWithBalance'
import UnlockButton from 'components/UnlockButton'
import CakeHarvestBalance from '../../Home/components/CakeHarvestBalance'
import CakeWalletBalance from '../../Home/components/CakeWalletBalance'
import { usePriceCakeBusd } from '../../../state/hooks'
import useTokenBalance from '../../../hooks/useTokenBalance'
import { getCakeAddress } from '../../../utils/addressHelpers'
import useAllEarnings from '../../../hooks/useAllEarnings'
import { getBalanceNumber } from '../../../utils/formatBalance'

const StyledFirst = styled(Card)`
  // margin-right: auto;
  padding: 30px;
  max-width: 40rem;
`
const Row = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin: 0.5rem 0 0.5rem 0;
`

const Action = styled.div`
  width: 100%;
  // background: grey;
  display: flex;
  justify-content: center;
`

const First = () => {
  const presaleContract = usePresaleContract()
  const [pendingTx, setPendingTx] = useState(false)

  const [unicornRemaining, setUnicornRemaining] = useState(0)
  const [myBalance, setMyBalance] = useState('0')

  const [unicornPrice, setUnicornPrice] = useState(0)

  const contractRaisingToken = useERC20('0x2791bca1f2de4661ed88a30c99a7a9449aa84174')

  const [isPresaleStarted,setPresaleStarted] = useState(false);
  const allowance = useIfoAllowance(contractRaisingToken, contracts.presale[137], pendingTx)
  const onApprove = useIfoApprove(contractRaisingToken, contracts.presale[137])




  const [maxBuy, setMaxBuy] = useState(0)
  const [endDate, setEndDate] = useState(0)
  const [startDate, setStartDate] = useState(0)
  const [inputAmount, setInputAmount] = useState('')

  const TranslateString = useI18n()
  const farmsWithBalance = useFarmsWithBalance()
  const cakeBalance = getBalanceNumber(useTokenBalance(getCakeAddress()))
  const usdcBalance = getBalanceNumber(useTokenBalance("0x2791bca1f2de4661ed88a30c99a7a9449aa84174"),6)

  const eggPrice = usePriceCakeBusd().toNumber()
  const allEarnings = useAllEarnings()

  const {account} = useWallet()
  const earningsSum = allEarnings.reduce((accum, earning) => {
    return accum + new BigNumber(earning).div(new BigNumber(10).pow(18)).toNumber()
  }, 0)

  const balancesWithValue = farmsWithBalance.filter((balanceType) => balanceType.balance.toNumber() > 0)

  // const { onReward } = useAllHarvest(balancesWithValue.map((farmWithBalance) => farmWithBalance.pid))

  const renderDate = (time) => {
    console.log('renderDate', time, Date.now() + 5000)
    return <Countdown date={new Date(time)} renderer={timeRenderer} />
  }

  useEffect(() => {
    if (usdcBalance) {
      setMyBalance(usdcBalance.toFixed(2).toString())

      console.log("dusdcBalance",usdcBalance)
    }
  }, [usdcBalance])

  useEffect(() => {
    console.log("allowance",allowance)

  }, [allowance])
  

  // Renderer callback with condition
  const timeRenderer = ({ days, hours, minutes, seconds }) => {
    return (
      <span>
        Days {days} Hours {hours} Mins {minutes} Secs {seconds}
      </span>
    )
  }

  useEffect(() => {
    const loadContractData = async (_presaleContract) => {
      // const [unicornRemaining, setUnicornRemaining] = useState(0)
      // const [maxBuy, setMaxBuy] = useState(0)
      // const [endDate, setEndDate] = useState(0)
      // const [startDate, setStartDate] = useState(0)

      let _unicornRemaining = await _presaleContract.methods.getTokenBalance().call()
      _unicornRemaining = getBalanceNumber(_unicornRemaining)
      let _maxBuy = await _presaleContract.methods.MAX_BUY_LIMIT().call()
      _maxBuy = getBalanceNumber(_maxBuy)

      const _startDate = await _presaleContract.methods.presaleStarts().call()
      const _endDate = await _presaleContract.methods.presaleTimeEnds().call()

      const price = await _presaleContract.methods._price().call()
      setUnicornPrice(getBalanceNumber(price,6))
      setUnicornRemaining(_unicornRemaining)
      setMaxBuy(_maxBuy)

      const _isPresaleStarted = await _presaleContract.methods.presaleStarts().call()

      setPresaleStarted(_isPresaleStarted< Date.now() / 1000);

      const now = new Date().valueOf()
      if (now < _startDate * 1000) {
        setStartDate(_startDate * 1000)
      }

      if (now < _endDate * 1000) {
        setEndDate(_endDate * 1000)
      }
    }
    loadContractData(presaleContract)
  }, [presaleContract])



  const renderButton = () => {
    if (allowance <= 0) {
      return (
        <Button
          fullWidth
          disabled={pendingTx}
          onClick={async () => {
            try {
              setPendingTx(true)
              await onApprove()
              setPendingTx(false)
            } catch (e) {
              setPendingTx(false)
              console.error(e)
            }
          }}
        >
          Approve
        </Button>
      )
    }
    
    
    if(!isPresaleStarted ){
     return <Button id="harvest-all" disabled fullWidth style={{ width: '100%' }}>
      Presale not started
    </Button>
    }
    return (
      <Button id="harvest-all" disabled={pendingTx} onClick={buyInPresale} fullWidth style={{ width: '100%' }}>
        {pendingTx ? TranslateString(548, 'Buyng TMCOIN') : TranslateString(999, `Buy TMCOIN`)}
      </Button>
    )
  }



  const buyInPresale = async () => {
    setPendingTx(true)
    try {
      const value = getWeb3().utils.toWei(inputAmount)
      await presaleContract.methods.buyToken(value).send({
        from: account,
      })
    } catch (error) {
      // TODO: find a way to handle when the user rejects transaction or it fails
    } finally {
      setPendingTx(false)
    }
  }

  return (
    <StyledFirst>
      <Heading size="lg" style={{ textAlign: 'center', marginBottom: '1rem' }}>
        UNICORN PRESALE PANEL
      </Heading>
      <Row>
        <Text fontSize="1rem">Remaining UNICORN:</Text>
        <Text fontSize="1.2rem" bold>
          {unicornRemaining}
        </Text>
      </Row>

      <Row>
        <Text fontSize="1rem">UNICORN Price:</Text>
        <Text fontSize="1.2rem" bold>
          ${unicornPrice} 
        </Text>
      </Row>

      <Row>
        <Text fontSize="1rem">Your UNICORN balance:</Text>
        <Text fontSize="1.2rem" bold>
          {cakeBalance}
        </Text>
      </Row>
      <br />

      <Row>
        <Text fontSize="1rem">Your USDC balance:</Text>
        <Text fontSize="1.2rem" bold>
          ${myBalance}
        </Text>
      </Row>
      <Row>
        <Text fontSize="1rem">You can buy up to:</Text>
        <Text fontSize="1.2rem" bold>
          {maxBuy}
        </Text>
      </Row>

      {startDate === 0 ? null : (
        <Row>
          <Text fontSize="1rem">Presale Starts:</Text>
          <Text fontSize="1.2rem" bold>
            <Countdown date={new Date(startDate)} renderer={timeRenderer} />
          </Text>
        </Row>
      )}

      {endDate === 0 ? null : (
        <Row>
          <Text fontSize="1rem">Presale Ends:</Text>
          <Text fontSize="1.2rem" bold>
            <Countdown date={new Date(endDate)} renderer={timeRenderer} />
          </Text>
        </Row>
      )}

      <BalanceInput
        max={myBalance}
        symbol="USDC"
        onChange={(e) => setInputAmount(e.currentTarget.value)}
        onSelectMax={() => {
          setInputAmount(myBalance)
        }}
        value={inputAmount}
      />

      <Action>
        {account ? (
          renderButton()
        ) : (
          <UnlockButton />
        )}
      </Action>
    </StyledFirst>
  )
}

export default First
